import {React, useContext, useEffect, useState} from 'react'
import ChatHeader from './ChatHeader'
import Messages from './Messages'
import Footer from './Footer'
import { AccountContext } from '../../../context/AccountProvider'
import { getConversation, newMessage } from '../../../service/api'

const ChatBox = () => {

  const {person, account, socket, setActiveUsers, activeUsers}=useContext(AccountContext);
  const [conversation, setConversation]=useState({});
  const [text, setText]=useState('');
  const [newMessageFlag, setNewMessageFlag]=useState(false);
  const [file, setFile]=useState();
  useEffect(()=>{
    const getConversationDetails=async ()=>{
      let data=await getConversation({senderId: account.sub, receiverId: person.sub});
      data && setConversation(data);
    }
    getConversationDetails();
  }, [person.sub, conversation._id]);

  useEffect(()=>{
    socket.current.on('removeUsers', (data)=>{
      setActiveUsers(data);
    })
  }, []);

  const sendText=async (e)=>{
    const code=e.which;
    if(code===13 && text!==''){
      let message={
        senderId: account.sub,
        receiverId: person.sub,
        conversationId: conversation._id,
        type: 'text',
        text: text
      }
      // console.log(message);
      if(activeUsers.find((user)=>user.sub===person.sub)){
        socket.current.emit('sendMessage', message);
      }
      e.target.value="";
      await newMessage(message);
      setText('');
      setNewMessageFlag(!newMessageFlag);
    }
  }

  return (
    <div style={{
      height: '100%',
      display: 'flex',
      flexDirection: 'column'
    }}>
        <ChatHeader person={person}/>
        <Messages person={person} account={account} newMessageFlag={newMessageFlag} />
        <Footer person={person} sendText={sendText} setText={setText} file={file} setFile={setFile} />
    </div>
  )
}

export default ChatBox