import React, { useState } from 'react'
import TagFacesIcon from '@mui/icons-material/TagFaces';
import AddIcon from '@mui/icons-material/Add';
import MicIcon from '@mui/icons-material/Mic';

const iconStyle={
    fontSize: '30px',
    cursor: 'pointer'
}

const Footer = ({sendText, setText, file, setFile}) => {

  const onFileChange=(e)=>{
    console.log(e);
    // setFile(e.target.files[0]);
    // set
  }

  return (
    <div style={{
        padding: '0 30px',
        height: '70px',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        backgroundColor: '#F1F2F4'
    }}>
        <TagFacesIcon style={iconStyle}/>
        <label htmlFor="fileInput">
        <AddIcon style={iconStyle}/>
        </label>
        <input type="file" style={{
          display: 'none'
        }} id='fileInput' onChange={(e)=>onFileChange(e)} />
        <input type="text" style={{
            height: '34%',
            width: '85%',
            fontSize: '17px',
            padding: '10px',
            border: 'none',
            outline: 'none',
            borderRadius: '10px'
        }} placeholder='Type a message' onChange={(e)=>{setText(e.target.value)}} onKeyDown={(e)=>{sendText(e)}} />
        <MicIcon style={iconStyle}/>
    </div>
  )
}

export default Footer