import {React, useContext} from 'react';
import { Dialog } from '@mui/material';
import { GoogleLogin } from '@react-oauth/google';
import jwt_decode from 'jwt-decode';
import { AccountContext } from '../../context/AccountProvider';
import { addUser } from '../../service/api';

const dialogStyle={
    height: '95%',
    marginTop: '12%',
    width: '60%',
    maxWidth: '100%',
    boxShadow: 'none',
    overflow: 'hidden'
}

const LoginDialog = () => {

    const {setAccount}=useContext(AccountContext);

    async function onLoginSuccess(response){
        const decodedResponse=jwt_decode(response.credential);
        setAccount(decodedResponse);
        await addUser(decodedResponse);
    }

    function onLoginError(response){
        console.log(response);
    }

  return (
    <Dialog open={true} PaperProps={{ sx: dialogStyle}} hideBackdrop={true}>
        <div style={{
            padding: '20px',
            display: 'flex',
            justifyContent: 'space-between'
        }}>
            <div className="left" style={{
                width: '45%'
            }}>
                <h2 style={{
                    fontWeight: 'inherit'
                }}>Use Whatsapp on your computer:</h2>
                <ol style={{
                    lineHeight: '30px'
                }}>
                    <li>Click on Login</li>
                    <li>Choose your account</li>
                    <img src="https://media.tenor.com/jb1PfocvqcAAAAAC/puneet-superstar-jhuk-ke-rehna.gif" width={'200px'} alt="" />
                </ol>
            </div>
            <div className="right" style={{
                width: '45%',
                height: '400px',
                position: 'relative',
                top: '200px'
            }}>
                <GoogleLogin
                    onSuccess={onLoginSuccess}
                    onError={onLoginError}
                />
            </div>
        </div>
    </Dialog>
  )
}

export default LoginDialog