import React from 'react'
import { formatDate } from '../../../utils/commonUtils';

const receivedStyle={
    backgroundColor: 'white',
    maxWidth: '60%',
    marginRight: 'auto',
    marginTop: '10px',
    marginBottom: '10px',
    padding: '5px',
    width: 'fit-content',
    borderRadius: '10px',
    wordBreak: 'break-word',
    display: 'flex',
    gap: '5px'
};

const sentStyle={
    backgroundColor: '#dcf8c6',
    maxWidth: '60%',
    marginLeft: 'auto',
    marginTop: '10px',
    marginBottom: '10px',
    padding: '5px',
    width: 'fit-content',
    borderRadius: '10px',
    wordBreak: 'break-word',
    display: 'flex',
    gap: '5px'
}

const Message = ({message, account}) => {
  return (
    <div style={message.senderId===account.sub ? sentStyle : receivedStyle}>
        <p style={{
            margin: 0,
            padding: 0
        }}>{message.text}</p>
        <span style={{
            fontSize: '10px',
            color: '#919191',
            marginTop: 'auto',
            wordBreak: 'keep-all'
        }}>{formatDate(message.createdAt)}</span>
    </div>
  )
}

export default Message