import {React, useContext} from 'react'
import MoreVertRoundedIcon from '@mui/icons-material/MoreVertRounded';
import SearchIcon from '@mui/icons-material/Search';
import { emptyProfilePicture } from '../../../constants/data';
import { AccountContext } from '../../../context/AccountProvider';
import './chatheader.css';

const ChatHeader = ({person}) => {

  const {activeUsers}=useContext(AccountContext);

  return (
    <div className='chat-header'>
      <div className="profile">
        <img src={person.picture} alt="dp" className="profile-pic" />
        <div className="text-container">
          <p className="name">{person.name}</p>
          <span className='online-status'>{activeUsers?.find((user)=>user.sub===person.sub) ? 'online' : 'offline'}</span>
        </div>
      </div>
      <div className="icon-wrapper-chat-header">
        <SearchIcon className='icons-chat-header'/>
        <MoreVertRoundedIcon className='icons'/>
      </div>
    </div>
  )
}

export default ChatHeader