import React, { useEffect, useState, useRef, useContext } from 'react'
import { getMessages } from '../../../service/api'
import { AccountContext } from '../../../context/AccountProvider';
import Message from './Message';

const Messages = ({account, person, newMessageFlag}) => {

  const [messages, setMessages]=useState([]);
  const {socket}=useContext(AccountContext);
  const [incomingMessage, setIncomingMessage]=useState(null);
  const scrollRef=useRef();

  useEffect(()=>{
    const getMessageDetails=async()=>{
      let data=await getMessages({senderId: account.sub, receiverId: person.sub});
      setMessages(data);
    }
    getMessageDetails();
  }, [person._id, newMessageFlag]);

  useEffect(()=>{
    scrollRef.current?.scrollIntoView({transition: 'smooth'});
  }, [messages]);

  useEffect(()=>{
    socket.current.on('getMessage', (data)=>{
      setIncomingMessage({
        ...data,
        createdAt: Date.now()
      })
    })
  }, []);

  useEffect(()=>{
    incomingMessage && setMessages((prev)=>[...prev, incomingMessage]);
  }, [incomingMessage]);

  return (
    <div style={{
      flex: '1',
      backgroundImage: `url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png')`,
      backgroundSize: '60%',
      padding: '0 30px',
      overflowY: 'scroll'
    }}>
      {
        messages && messages.map((message)=>{
          return <div ref={scrollRef}><Message message={message} account={account} /></div>
        })
      }
    </div>
  )
}

export default Messages