import axios from 'axios';

const baseUrl='';

export const addUser= async (data)=>{
    try{
        await axios.post(`${baseUrl}/addUser`, data);
    }catch(err){
        console.log(err);
    }
}

export const getUsers=async ()=>{
    try{
        let response=await axios.get(`${baseUrl}/getUsers`);
        // console.log(response);
        return response.data;
    }catch(err){
        console.log(err.message);
    }
}

export const setConversation=async (data)=>{
    try{
        await axios.post(`${baseUrl}/conversation/add`, data);
    }catch(err){
        console.log(err.message);
    }
}

export const getConversation=async (data)=>{
    try{
        let res=await axios.post(`${baseUrl}/conversation/get`, data);
        return res.data;
    }catch(err){
        console.log(err.message);
    }
}

export const newMessage=async(data)=>{
    try{
        await axios.post(`${baseUrl}/message/add`, data);
    }catch(err){
        console.log(err.message);
    }
}

export const getMessages=async(data)=>{
    try{
        let response=await axios.post(`${baseUrl}/message/get`, data);
        return response.data;
    }catch(err){
        console.log(err.message);
    }
}