import React from 'react'
import { emptyChatImage } from '../../../constants/data'

const EmptyChat = () => {
  return (
    <div style={{
      height: '100%'
    }}>
      <div className="container" style={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100%'
      }}>
        <img src={emptyChatImage} alt="" style={{
          width: '500px'
        }} />
        <h1 style={{
          fontWeight: 300,
          color: '#3b4a54'
        }}>WhatsApp Web</h1>
        <p style={{
          color: '#667781'
        }}>Send and receive messages without keeping your phone online.</p>
        <p style={{
          color: '#667781'
        }}>Use WhatsApp on up to 4 linked devices and 1 phone at the same time.</p>
      </div>
    </div>
  )
}

export default EmptyChat